@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}
body {
  font-family: 'Space Mono', monospace;
  background: hsl(185, 41%, 84%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  overflow: hidden;
  margin: 0;
}

@media (max-width: 376px) {
  body {
    overflow-y: auto;
  }
}

.number-input {
  display: flex;
  width: 350px;
  height: 100px;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  padding-left: 15px;
}
.number-input h6 {
  font-size: 14px;
  margin: 5px;
  font-weight: 700;
  color: hsl(183, 100%, 15%);
  display: block;
  width: 350px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: left;
}

.number-input-container {
  display: flex;
  margin: 0;
  padding: 0;
  width: 325px;
  border: solid hsl(189, 41%, 97%);
  border-radius: 5px;
  background-color: hsl(189, 41%, 97%);
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-evenly;
  align-items: center;
}

.number-input img {
  position: relative;
  left: 25px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.number-input input {
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
  text-align: right;
  color: hsl(183, 100%, 15%);
  background-color: hsla(0, 100%, 50%, 0);
  border: none;
  padding: 0 15px;
  outline: none;
}
.number-input-container:hover {
  border: solid hsl(172, 67%, 45%);
}
.number-input input::-webkit-input-placeholder {
  color: hsl(184, 14%, 56%);
}
.number-input input:-ms-input-placeholder {
  color: hsl(184, 14%, 56%);
}
.number-input input::placeholder {
  color: hsl(184, 14%, 56%);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='number'] {
  -moz-appearance: textfield;
}

.tip-amount button {
  background-color: hsl(183, 100%, 15%);
  border-radius: 5px;
  border: none;
  color: hsl(0, 0%, 100%);
  cursor: pointer;
  font-family: inherit;
  font-size: 24px;
  font-weight: 400;
  width: 100px;
  height: 40px;
  margin: 5px 0;
}
.tip-amount button:hover {
  background-color: hsl(173, 61%, 77%);
  color: hsl(183, 100%, 15%);
}

@media (max-width: 481px) {
  .tip-amount button {
    width: 140px;
    height: 45px;
  }
}

.tip-custom button {
  background-color: hsl(189, 41%, 97%);
  border-radius: 5px;
  border: none;
  color: hsl(183, 100%, 15%);
  cursor: pointer;
  font-family: inherit;
  font-size: 24px;
  font-weight: 400;
  width: 100px;
  height: 40px;
}
.tip-custom button:hover {
  background-color: hsl(173, 61%, 77%);
  color: hsl(183, 100%, 15%);
  margin: 5px 0;
}

.tip-custom input {
  width: 100px;
  height: 40px;
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
  text-align: right;
  border: solid hsl(189, 41%, 97%);
  border-radius: 5px;
  background-color: hsl(189, 41%, 97%);
  color: hsl(183, 100%, 15%);

  padding: 0 15px;
}
.tip-custom input:hover {
  border: solid hsl(172, 67%, 45%);
  outline: none;
}
.tip-custom input::-webkit-input-placeholder {
  color: hsl(184, 14%, 56%);
}
.tip-custom input:-ms-input-placeholder {
  color: hsl(184, 14%, 56%);
}
.tip-custom input::placeholder {
  color: hsl(184, 14%, 56%);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='number'] {
  -moz-appearance: textfield;
}

@media (max-width: 481px) {
  .tip-custom button,
  .tip-custom input {
    width: 140px;
    height: 45px;
  }
}

.tip-input {
  width: 350px;
}
.tip-input h2 {
  font-size: 14px;
  font-weight: 700;
  color: hsl(183, 100%, 15%);
  margin-bottom: 5px;
  width: 350px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: left;
  padding-left: 20px;
}
.tip-input-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
  align-content: center;
  justify-content: space-evenly;
}

.per-person {
  width: 300px;
  display: flex;
  flex-direction: row;
  text-align: left;
  justify-content: space-between;
  align-items: center;
}
.per-person-title-container {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.per-person-title {
  font-weight: 700;
  color: hsl(0, 0%, 100%);
  margin: 0;
}
.per-person-text {
  color: hsl(185, 41%, 84%);
  font-size: 14px;
  font-weight: 700;
  margin: 0;
}
.per-person-output {
  color: hsl(172, 67%, 45%);
  font-weight: 700;
  font-size: 36px;
  margin: 0;
}

.button {
  background-color: hsl(172, 67%, 45%);
  border-radius: 5px;
  border: none;
  color: hsl(183, 100%, 15%);
  cursor: pointer;
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
  width: 300px;
  height: 40px;
  margin: 5px 0;
}
.button:hover {
  background-color: hsl(173, 61%, 77%);
  color: hsl(183, 100%, 15%);
}
.button:disabled {
  background-color: hsl(183, 79%, 24%);
}

.tip-calculator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tip-calculator-logo {
  pointer-events: none;
  margin: 20px;
}
.tip-calculator-card {
  background-color: hsl(0, 0%, 100%);
  padding: 20px 25px;
  border-radius: 25px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 850;
  /* max-width: 850px; */
  margin: auto;
}
.tip-calculator-input {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  /* background-color: rgb(158, 60, 60); */
}
.tip-calculator-output {
  background-color: hsl(183, 100%, 15%);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  text-align: center;
  align-content: center;
  justify-content: space-evenly;
  margin: 0px;
  padding: 0px 0px;
  border-radius: 20px;
  width: 400px;
  height: 400px;
}
.attribution {
  margin-top: 30px;
}

@media (max-width: 481px) {
  .tip-calculator {
    position: relative;
    top: 25px;
  }
  .tip-calculator-card {
    flex-direction: column;
    width: 100vw;
    height: 100%;
  }
  .tip-calculator-output {
    width: 328px;
    height: 255px;
    justify-content: space-evenly;
  }
  .attribution {
    margin-top: 10px;
  }
}

